@font-face {
  font-family: "Cannu";
  src: url("./assets/fonts/cannu.ttf") format("truetype");
}
@font-face {
  font-family: "Sansation";
  src: url("./assets/fonts/sansation_light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "Sansation";
  src: url("./assets/fonts/sansation_light_italic.ttf") format("truetype");
  font-weight: 300;
  font-style: italic;
}
@font-face {
  font-family: "Sansation";
  src: url("./assets/fonts/sansation_regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Sansation";
  src: url("./assets/fonts/sansation_italic.ttf") format("truetype");
  font-weight: 400;
  font-style: italic;
}
@font-face {
  font-family: "Sansation";
  src: url("./assets/fonts/sansation_bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Sansation";
  src: url("./assets/fonts/sansation_bold_italic.ttf") format("truetype");
  font-weight: 700;
  font-style: italic;
}
html {
  box-sizing: border-box;
  font-size: 100%;
  scroll-behavior: smooth;
}

:root {
  --text: "Sansation", sans-serif;
  --title: "Cannu", serif;
  --black: #111;
  --blackTwo: #333;
  --white: #fff;
  --whiteTwo: #f7f7f7;
  --gray: #828282;
  --grayTwo: #bdbdbd;
  --green: #27ae60;
  --brown: rgba(126, 67, 37, 0.9);
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  color: var(--black);
  font-family: var(--text);
  font-size: 1rem;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  color: var(--black);
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

@media (min-width: 100em) {
  .container {
    max-width: 1520px;
  }
}

.siteText {
  font-size: 18px;
  line-height: 30px;
}

.preTitle {
  font-size: 18px;
  line-height: 20px;
}

.siteTitle {
  font-size: 60px;
  line-height: 72px;
  letter-spacing: -0.04em;
}

.siteButton {
  position: relative;
  display: block;
  max-width: max-content;
}
.siteButton:hover {
  color: currentColor;
}
.siteButton:hover span {
  background: var(--whiteTwo);
}
.siteButton:hover svg path {
  fill: var(--whiteTwo);
}
.siteButton span {
  display: block;
  width: 228px;
  padding: 13px 5px 10px;
  background: var(--white);
  text-align: center;
  font-weight: 400;
  font-size: 18px;
  letter-spacing: -0.02em;
  line-height: 30px;
  color: var(--black);
  transition: all 0.3s;
  clip-path: polygon(0 0, 100% 0, 93% 100%, 0 100%);
}
.siteButton svg {
  position: absolute;
  top: 0;
  transition: all 0.3s;
}
.siteButton svg:first-child {
  left: -8px;
}
.siteButton svg:last-child {
  right: 0;
  transform: translateX(50%);
}

.header {
  background: rgba(51, 51, 51, 0.4);
  backdrop-filter: blur(30px);
  padding-top: 10px;
  padding-bottom: 9px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}
.header.active {
  background: none;
}
.header__container {
  display: grid;
  gap: 20px;
  align-items: center;
  grid-template-columns: 1fr auto;
}
@media (min-width: 36em) {
  .header__container {
    grid-template-columns: auto 1fr auto;
    gap: 60px;
  }
}
.header__link.active {
  opacity: 0;
}
.header__social {
  position: relative;
  padding-left: 1px;
}
.header__social.active {
  opacity: 0;
}
@media (max-width: 35.9375em) {
  .header__social {
    display: none;
  }
}
.header__social::after {
  content: "";
  display: block;
  width: 1px;
  height: 50px;
  background: var(--white);
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-30px, -50%);
}
.header__social__list {
  display: flex;
  align-items: center;
  gap: 20px;
}
.header__social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 25px;
  color: var(--white);
  transition: hover 0.3s;
}
.header__social__link:hover {
  color: var(--whiteTwo);
}

@keyframes showMenu {
  to {
    opacity: 1;
  }
}
.menu {
  position: relative;
}
@media (max-width: 74.9375em) {
  .menu__wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: none;
    background: rgba(51, 51, 51, 0.4);
    backdrop-filter: blur(30px);
    z-index: 990;
    padding: 100px 30px;
    height: 100vh;
    overflow: auto;
  }
  .menu__wrapper > div {
    margin: auto;
    width: 100%;
  }
  .menu__wrapper.active {
    display: flex;
    opacity: 0;
    animation: showMenu 0.3s forwards;
  }
}
.menu__list {
  display: grid;
  align-items: center;
  gap: 30px;
}
@media (min-width: 75em) {
  .menu__list {
    display: flex;
  }
}
.menu__link {
  display: block;
  color: var(--white);
  font-size: 18px;
  line-height: 20px;
  transition: all 0.3s;
}
.menu__link:hover {
  color: var(--whiteTwo);
}
@media (max-width: 74.9375em) {
  .menu__link {
    text-align: center;
  }
}
.menu__button {
  appearance: none;
  border: none;
  padding: 0;
  cursor: pointer;
  z-index: 995;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  width: 24px;
  height: 24px;
  background: none;
}
.menu__button::before {
  content: "";
  display: block;
  width: 100%;
  height: 0.1875rem;
  background: var(--white);
  transition: all 0.3s;
  box-shadow: 0 0.375rem var(--white), 0 -0.375rem var(--white);
}
.menu__button::after {
  content: "";
  display: block;
  width: 100%;
  height: 0.1875rem;
  background: var(--white);
  transition: all 0.3s;
  position: absolute;
}
.menu__button.active::before {
  box-shadow: none;
  transform: rotate(45deg);
}
.menu__button.active::after {
  transform: rotate(-45deg);
}
@media (min-width: 75em) {
  .menu__button {
    display: none;
  }
}
.menu__social__list {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
}
@media (min-width: 75em) {
  .menu__social__list {
    display: none;
  }
}
.menu__social__link {
  color: var(--white);
  font-size: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.menu__social__link:hover {
  color: var(--whiteTwo);
}
.menu__divisor {
  margin-top: 60px;
  margin-bottom: 60px;
  display: grid;
  align-items: center;
  width: 100%;
  grid-template-columns: 1fr auto 1fr;
  gap: 60px;
}
.menu__divisor::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background-color: var(--white);
}
.menu__divisor::before {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background-color: var(--white);
}
@media (min-width: 75em) {
  .menu__divisor {
    display: none;
  }
}
.menu__contact {
  margin: 0 auto;
}